
import { defineComponent, onMounted, ref, watch } from "vue";

export default defineComponent({
  props: {
    text: {
      type: String,
      default: ""
    },
    placement: {
      type: String,
      default: "top"
    },
    isVisible: {
      type: Boolean,
      default: false
    },
    isHoverControlled: {
      type: Boolean,
      default: true
    },
    offset: {
      type: Array as () => number[],
      default: () => [0, 5]
    }
  },
  setup(props) {
    const slot = ref<HTMLElement>();
    const tooltip = ref<HTMLElement>();
    const isHovered = ref(false);

    const showTooltip = () => {
      if (slot.value && tooltip.value) {
        //todo: заменить на antdv
        /*console.log(slot.value, tooltip.value, {
          placement: props.placement,
          modifiers: [
            { name: 'flip', enabled: true, options: { boundary: [] } },
            {
              name: 'offset',
              options: {
                offset: props.offset,
              },
            },
            {
              name: 'arrow',
              options: {
                padding: 15,
              },
            },
          ],
        });*/
      }
    };

    const handleHover = () => {
      isHovered.value = true;
      showTooltip();
    };

    watch(
      () => props.isVisible,
      value => {
        if (value) {
          showTooltip();
        }
      }
    );

    const handleUnhover = () => {
      isHovered.value = false;
    };

    onMounted(() => {
      if (props.isHoverControlled) {
        slot.value?.addEventListener("mouseenter", handleHover);
        slot.value?.addEventListener("mouseleave", handleUnhover);
      }
    });

    return { slot, tooltip, isHovered };
  }
});
